import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ViewController, AlertController  } from 'ionic-angular';
import { FirebaseProvider } from '../../providers/firebase';
import { LoadingProvider } from '../../providers/loading';
import { AngularFirestoreCollection } from 'angularfire2/firestore';
import { Assunto } from '../../model/assunto';

@IonicPage()
@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html',
})
export class MenuPage {
  assuntos: any;
  modal = false;
  
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private firebaseProvider: FirebaseProvider,
    private loadingProvider: LoadingProvider,
    private modalCtrl: ModalController,
    private viewCtrl: ViewController,
	public alertCtrl: AlertController
  ) {
    this.getAssuntos();
    this.loadingProvider.present();
    this.modal = this.navParams.get('modal');
	
  }

  //Refresh page
  refresh(refresher) {
    refresher.complete();
    this.navCtrl.setRoot(this.navCtrl.getActive().component);
  }

  //Close modal
  close() {
    this.viewCtrl.dismiss()
  }

  //Open assunto page
  open(p) {
    let modal = this.modalCtrl.create('AssuntosPage', { assuntos: p });
    modal.present();
  }

  //Open cart
  openCart() {
    let modal = this.modalCtrl.create('CartPage', { modal: true });
    modal.present();
  }
  //Open Details
  openDatails(value) {
	console.log(value);
	  
    let modal = this.modalCtrl.create('AssuntoDetailsPage', {data : value});
    modal.present();
  }


  //List all assuntos to slides
  getAssuntos() {
    this.firebaseProvider.getAssuntos()
      .subscribe((res) => {
        this.loadingProvider.dismiss();	
        this.assuntos = res;
      })
  }
}




